.#{$parentPrefix}header {
    &.#{$prefix}absolute {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        right: 0;
        z-index: 9;
    }
    &.#{$prefix}stuck {
        position: fixed;
    }
}

.switch2 .#{$parentPrefix}header {
    padding: 0 45px;
}
